    #intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.bg{
    position: absolute;
    top: 05rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 60vh;
    border-radius: 10px;
}

.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size: 1.75rem;
    font-weight: 100;
}

.introName{
    color: yellow;
}

.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.btn{
    background-color: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    cursor: pointer;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width: 840px){
    .bg{
        right: -10vw;
        height: 40vh;
        top: 10rem;

    }
    .introContent{
        font-size: 5vw;
    }
    .hello{
        font-size: 2.5vw;
    }
    .introPara{
        font-size: 2vw;
    }
}

@media screen and (max-width: 480px){
    .bg{
        right: -20vw;
        height: 30vh;
        top: 2rem;
    }
}
